.about {
  padding: 8rem 0rem;
}

.about__inner {
  display: flex;
  justify-content: flex-start;
}

.about__nav {
  padding-top: 7rem;
  width: 20%;
}

.about__brand {
  width: 40%;
  margin-left: 10rem;
}

.about__nav-item {
  margin-bottom: 1.5rem;

  &:last-child {
    margin-bottom: 0;
  }
}

.about__nav-link {
  font-size: 1.7rem;
  color: rgba(0, 0, 0, 0.4);
  font-family: 'Raleway';
  font-weight: 500;

  &--active {
    color: $black;
  }
}

.about__brand-title {
  font-size: 5.5rem;
  font-family: 'Raleway';
  font-weight: 500;
}

.about__brand-subtitle {
  font-weight: 3rem;
  color: $black;
  font-family: 'Raleway';
  font-weight: 500;
}

.about__brand-text {
  font-size: 1.7rem;
  font-weight: 400;
  font-family: 'Inter';
  line-height: 1.3;
}